.contactForm-inputWrapper {
  margin-top: 10px;
  border-bottom: 1px solid #3c3c3c;
}

.contactForm-formBox {
  display: flex;
  justify-content: center;
  input {
    border: none;
    background-color: transparent;
    margin-bottom: 5px;
    &::placeholder {
      opacity: 1;
      color: #3c3c3c26;
    }
  }
}

.contactForm-inputBox {
  margin-top: 15px;
  margin-left: 40px;
  display: flex;
  flex-direction: column;
  margin-bottom: 45px;
  label {
    font-weight: 600;
  }
}

.contactForm-messageBox {
  textarea {
    border: none;
    border-bottom: 1px solid #3c3c3c;
    background-color: transparent;
    margin-top: 15px;
    margin-bottom: 10px;
    height: 100px;
    font-size: 1.125rem;
    &::placeholder {
      opacity: 1;
      color: #3c3c3c26;
    }
  }
}

.contactForm-errors {
  margin-right: 30px;
  padding: 10px 0;
  color: red;
}

.contactForm-buttonWrapper {
  display: flex;
  flex-direction: column;
}

.contactForm-submit {
  text-transform: uppercase;
  font-size: 1.125rem;
  font-weight: 300;
  color: #3c3c3c;
  border: 0.75px solid #3c3c3c;
  background-color: #fff;
  align-self: flex-end;
  width: 150px;
  height: 50px;
}

.contactForm-footer {
  font-size: 1.125rem;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  img {
    margin: 10px;
  }
}

@media (max-width: 992px) {
  .contactForm-formBox {
    flex-direction: column;
  }
  .contactForm-inputBox {
    margin: 15px 15px 15px 15px;
  }
  .contactForm-submit {
    align-self: center;
    width: 100%;
    height: 50px;
    margin: 20px 0 30px 0;
  }
}
