.login {
  color: #3c3c3c;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login-header {
  margin-top: 250px;
}

.login-decoration {
  margin-top: 20px;
  margin-bottom: 70px;
}

.login-inputBox {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
  font-weight: 600;
  &:last-child {
    margin-bottom: 0;
  }
  input {
    margin-top: 15px;
    width: 250px;
    background-color: #f0f1f1;
    border: none;
    border-bottom: 0.75px solid #3c3c3c;
  }
}

.login-form {
  padding: 40px 70px;
  background-color: #f0f1f1;
}

.login-buttons {
  width: 40%;
  display: flex;
  justify-content: space-between;
  button {
    margin-top: 40px;
  }
}

.login-button-active,
.login-button-inactive {
  background-color: #fff;
  color: #3c3c3c;
  border: none;
  width: 120px;
  height: 50px;
  font-size: 1.125rem;
  a {
    font-weight: 300;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
}

.login-button-active {
  border: 0.75px solid #3c3c3c;
}

.login-loggout-buttonWrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.login-decoration {
  margin-top: 20px;
  margin-bottom: 40px;
}

@media (max-width: 992px) {
  .login-button-inactive {
    display: none;
  }
  .login-form {
    background-color: transparent;
  }
  .login-inputBox {
    input {
      background-color: transparent;
    }
  }
  .login-header {
    margin-top: 80px;
    align-self: center;
    font-size: 1.7rem;
  }
  .login-decoration {
    margin: 30px 0 50px 0;
    align-self: center;
    width: 14rem;
  }
  .login-buttons {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .login-button-active {
    text-transform: uppercase;
    margin: 10px 0 10px 0;
    width: 310px;
    font-size: 1.125rem;
    font-weight: 300;
  }
}
