.homeFundations-headerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.homeFundations-header {
  margin-top: 70px;
}

.homeFundations-decoration {
  margin-top: 20px;
  margin-bottom: 50px;
}

.homeFundations-navigation {
  max-width: 720px;
  margin: 0 auto;
  margin-bottom: 70px;
  display: flex;
  justify-content: space-evenly;
}

.homeFundations-navButton {
  width: 180px;
  margin-right: 60px;
  margin-left: 60px;
  height: 80px;
  font-size: 1.5rem;
  border: none;
  background-color: #fff;
}

@media (max-width: 992px) {
  .homeFundations {
    padding: 60px 20px;
  }
  .homeFundations-navigation {
    flex-direction: column;
    justify-content: flex-start;
    margin: 0 0 20px 0;
  }
  .homeFundations-navButton {
    text-align: left;
    text-decoration: underline;
    font-size: 1.3rem;
    height: 50px;
    width: 100%;
    margin: 0;
    padding: 0 20px;
  }
  .homeFundations-header {
    margin: 0;
    align-self: center;
    font-size: 1.7rem;
  }
  .homeFundations-decoration {
    margin: 30px 0 50px 0;
    align-self: center;
    width: 14rem;
  }
}
