.appHeader-navMenuDesktopTop,
.appHeader-navMenuDesktopScrolled {
  top: 0;
  position: fixed;
  background-color: #fff;
  z-index: 2;
}

.appHeader-navMenuDesktopTop {
  right: 150px;
}

.appHeader-navMenuDesktopScrolled {
  padding-top: 10px;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  left: 50%;
  transform: translate(-50%, 0);
  padding-bottom: 20px;
}

.appHeader-navMenuMobile {
  display: none;
}

.appHeader-authMenu,
.appHeader-contentMenu {
  display: flex;
  justify-content: flex-end;
  li {
    padding: 10px;
  }
  .appHeader-currentUserDisplayed {
    cursor: default;
  }
}

.appHeader-authMenu {
  color: #737373;
  font-size: 0.875rem;
}

.appHeader-contentMenu {
  font-size: 1.125rem;
  cursor: pointer;
}

.appHeader-hiddenElement {
  display: none;
}

@media (max-width: 992px) {
  @keyframes expandSideMenu {
    from {
      left: -80%;
    }
    to {
      left: 0;
    }
  }
  @keyframes applyBackgroundFilter {
    from {
      opacity: 0;
    }
    to {
      opacity: 0.6;
    }
  }

  .appHeader-navMenuDesktopTop,
  .appHeader-navMenuDesktopScrolled {
    display: none;
  }
  .appHeader-navMenuMobile {
    background-color: #fff;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 3;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 1px 6px 0 rgba(32, 33, 36, 0.28);
  }
  .appHeader-sideMenu {
    display: block;
    position: absolute;
    background-color: #fff;
    opacity: 1;
    top: 0;
    left: 0;
    height: 100vh;
    width: 80vw;
    z-index: 5;
    animation-name: expandSideMenu;
    animation-duration: 0.3s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
  }
  .appHeader-backgroundFilter {
    display: block;
    position: absolute;
    background-color: black;
    opacity: 0.5;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: 4;
    animation-name: applyBackgroundFilter;
    animation-duration: 0.3s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
  }
  .appHeader-hamburger {
    background-image: url("../../assets/HamburgerMenu.svg");
    width: 24px;
    height: 24px;
    margin: 12px;
  }

  .appHeader-closeSideMenuIcon {
    background-image: url("../../assets/closeIcon.svg");
    width: 24px;
    height: 24px;
    margin: 12px;
  }

  .appHeader-contentMenu {
    display: flex;
    flex-direction: column;
  }

  .appHeader-authMenu {
    margin-right: 10px;
    li {
      display: flex;
      align-items: center;
    }
  }
}
