.homeMain-background {
  background-image: url(../../../assets/Home-Hero-Image.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: -150% 0;
}

.homeMain-desktop {
  margin-top: 50px;
  display: flex;
  justify-content: flex-end;
}

.homeMain-image {
  width: 400px;
  height: 400px;
}

.homeMain-content {
  margin-top: 340px;
  margin-bottom: 270px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homeMain-decoration {
  align-self: center;
  margin-top: 25px;
  margin-bottom: 65px;
}

.homeMain-buttonsBox {
  margin-top: 50px;
  margin: 20px;
  display: flex;
  justify-content: center;
}

.homeMain-button {
  margin: 0 15px;
}
.homeMain-mobile {
  display: none;
}

@media (max-width: 992px) {
  .homeMain {
    padding-top: 120px;
    padding-bottom: 60px;
    background-color: #f0f1f1;
  }
  .homeMain-desktop {
    display: none;
  }
  .homeMain-mobile {
    margin: 0 20px 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .homeMain-buttonsBox {
    margin: 10px 0 0 0;
    flex-direction: column;
    align-items: center;
  }
  .homeMain-button {
    display: flex;
    justify-content: center;
    align-items: center;
    a {
      display: block;
      width: 60%;
    }
  }
  .homeMain-lowerHeader,
  .homeMain-upperHeader {
    font-size: 1.7rem;
    line-height: normal;
  }
  .homeMain-lowerHeader {
    margin-top: 30px;
  }
  .homeMain-decoration {
    margin: 30px 0 50px 0;
    align-self: center;
    width: 14rem;
  }
  .homeMain-button {
    margin: 10px 0 10px 0;
    width: 100%;
    a {
      font-size: 1.125rem;
      font-weight: 300;
      width: 100%;
    }
  }
}
