.homeFundationsDisplay {
  display: flex;
  flex-direction: column;
}

.homeFundationsDisplay-header {
  max-width: 680px;
  margin: 0 auto;
  line-height: 1.75rem;
  font-size: 1.375rem;
  text-align: justify;
  text-align-last: center;
  margin-bottom: 60px;
}

.homeFundationsDisplay-list {
  li {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #3c3c3c;
    padding: 20px;
  }
  li:last-child {
    border-bottom: none;
  }
}

.homeFundationsDisplay-fundationName {
  font-size: 1.625rem;
  margin-bottom: 5px;
  font-weight: 300;
  font-family: $font-alt;
  color: #3c3c3c;
}

.homeFundationsDisplay-fundationDescription,
.homeFundationsDisplay-fundationNeeds {
  font-size: 1.125rem;
  font-weight: 300;
  font-family: $font-alt;
  color: #3c3c3c;
}

.homeFundationsDisplay-fundationNeeds {
  align-self: center;
}

@media (max-width: 992px) {
  .homeFundationsDisplay-header {
    display: none;
  }
  .homeFundationsDisplay-fundationName {
    font-size: 1.25rem;
    padding: 10px 0;
  }

  .homeFundationsDisplay-fundationDescription,
  .homeFundationsDisplay-fundationNeeds {
    font-size: 1rem;
  }
  .homeFundationsDisplay-fundationNeeds {
    margin-left: 30px;
  }
  .pagination-container {
    padding-top: 20px;
  }
}
