.homeDonate {
  display: flex;
  flex-direction: column;
}

.homeDonate-headerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.homeDonate-header {
  margin-top: 55px;
}

.homeDonate-decoration {
  margin-top: 20px;
}

.homeDonate-background {
  margin-top: 40px;
  background-color: #f0f1f1;
}

.homeDonate-instructions {
  display: flex;
  justify-content: space-evenly;
  h4 {
    text-align: center;
    // margin-top:10px;
    // margin-bottom:20px;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  p {
    text-align: center;
    width: 65%;
  }
  img {
    height: 100px;
    width: 100px;
  }
}
.homeDonate-instruction {
  margin: 70px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.homeDonate-button {
  margin-top: 35px;
  align-self: center;
}

@media (max-width: 992px) {
  .homeDonate {
    padding-top: 60px;
    padding-bottom: 60px;
  }
  .homeDonate-background {
    margin-top: 0;
    padding: 15px 0;
    background-color: #fff;
  }
  .homeDonate-instructions {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 20px;
  }
  .homeDonate-instruction {
    margin: 0;
    h4 {
      width: 70%;
    }
    p {
      display: none;
    }
  }
  .homeDonate-header {
    margin-top: 0;
    font-size: 1.7rem;
    line-height: normal;
  }
  .homeDonate-decoration {
    margin: 30px 0 50px 0;
    align-self: center;
    width: 14rem;
  }
  .homeDonate-button {
    margin: 10px 0;
    a {
      font-size: 1.125rem;
      font-weight: 300;
      width: 100%;
    }
  }
}
